import React from 'react';
import "./NavBar.css";
import {useNavigate} from "react-router-dom";

function Navbar() {
  const history=useNavigate();
  return (
    <div className='navbar'>
        <img onClick={()=>history("/")}  className='logo' src="https://upload.wikimedia.org/wikipedia/commons/thumb/0/08/Netflix_2015_logo.svg/1920px-Netflix_2015_logo.svg.png" alt="netflix logo"></img>
       <img onClick={()=>history("/profile")} className="avatar"src="https://i.pinimg.com/originals/0d/dc/ca/0ddccae723d85a703b798a5e682c23c1.png" alt="avathar"/>
    </div>
  )
}

export default Navbar;